import React from "react";
import "./Gnb.scss";

import { Link, Route, Switch } from "react-router-dom";
const Gnb = () => {
  return (
    <div className="header-container">
      <div className="gnb-container">
        <div className="gnb-inner">
          <ul className="gnb-menu-section">
            <li className="gnb-menu-list">
              <Link className="gnb-list-link" to="/">
                홈
              </Link>
            </li>
            <li className="gnb-menu-list">
              <Link className="gnb-list-link" to="/event">
                이벤트
              </Link>
            </li>
            <li className="gnb-menu-list">
              <Link className="gnb-list-link" to="/best">
                베스트
              </Link>
            </li>
            <li className="gnb-menu-list">
              <Link className="gnb-list-link" to="/shopping">
                기획전
              </Link>
            </li>
            <li className="gnb-menu-list">
              <Link className="gnb-list-link" to="/coupon">
                쿠폰/혜택
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Gnb;
