import React, { useState } from "react";
import Gnb from "../Gnb/Gnb.js";
import Data from "./Data.js";
import "./Event.scss";

const Event = () => {
  const [eventInfo, setEventInfo] = useState(Data);

  return (
    <div className="event-product-container">
      <Gnb />
      <div className="event-container-inner">
        <h2 className="event-text">EVENT</h2>
        <ul className="product-list">
          <li className="product-list-inner">
            <a className="product-image-container" href="">
              <img className="product-image" src={`${process.env.PUBLIC_URL + "/event/brand1.jpg"}`} />
              <div class="product-hashtag">
                <span className="product-name">#민감피부케어</span>
                <br />
                <span className="product-name">#국민쿠션</span>
                <br />
                <span className="product-name">#주름세럼</span>
              </div>
            </a>

            <EventProduct eventInfo={eventInfo[0]} />
          </li>

          <li className="product-list-inner">
            <a className="product-image-container" href="">
              <img className="product-image" src={`${process.env.PUBLIC_URL + "/event/brand2.jpg"}`} />
              <div class="product-hashtag">
                <span className="product-name">#민감피부케어</span>
                <br />
                <span className="product-name">#국민쿠션</span>
                <br />
                <span className="product-name">#주름세럼</span>
              </div>
            </a>
            <EventProduct eventInfo={eventInfo[1]} />
          </li>

          <li className="product-list-inner">
            <a className="product-image-container" href="">
              <img className="product-image" src={`${process.env.PUBLIC_URL + "/event/brand3.jpg"}`} />
              <div class="product-hashtag">
                <span className="product-name">#한국적자연주의</span>
                <br />
                <span className="product-name">#일상적이로움</span>
                <br />
                <span className="product-name">#자연을닮은한율</span>
              </div>
            </a>
            <EventProduct eventInfo={eventInfo[2]} />
          </li>
        </ul>
        <ul className="product-list">
          <li className="product-list-inner">
            <a className="product-image-container" href="">
              <img className="product-image" src={`${process.env.PUBLIC_URL + "/event/brand1.jpg"}`} />
              <div class="product-hashtag">
                <span className="product-name">#스파이시누드밤</span>
                <br />
                <span className="product-name">#스파이시글로스</span>
                <br />
                <span className="product-name">#파우더매트</span>
              </div>
            </a>

            <EventProduct eventInfo={eventInfo[0]} />
          </li>

          <li className="product-list-inner">
            <a className="product-image-container" href="">
              <img className="product-image" src={`${process.env.PUBLIC_URL + "/event/brand2.jpg"}`} />
              <div class="product-hashtag">
                <span className="product-name">#민감피부케어</span>
                <br />
                <span className="product-name">#국민쿠션</span>
                <br />
                <span className="product-name">#주름세럼</span>
              </div>
            </a>
            <EventProduct eventInfo={eventInfo[1]} />
          </li>

          <li className="product-list-inner">
            <a className="product-image-container" href="">
              <img className="product-image" src={`${process.env.PUBLIC_URL + "/event/brand3.jpg"}`} />
              <div class="product-hashtag">
                <span className="product-name">#한국적자연주의</span>
                <br />
                <span className="product-name">#일상적이로움</span>
                <br />
                <span className="product-name">#자연을닮은한율</span>
              </div>
            </a>
            <EventProduct eventInfo={eventInfo[2]} />
          </li>
        </ul>
      </div>
    </div>
  );
};

function EventProduct(props) {
  return (
    <>
      <div class="product-text-wrap">
        <em className="product-event">{props.eventInfo.EventLogo}</em>
        <p className="event-text">{props.eventInfo.EventType}</p>
        <span className="event-date">{props.eventInfo.EventDate}</span>
      </div>
    </>
  );
}

export default Event;
