import React from "react";
import "./User.scss";
import { Link, Route, Switch } from "react-router-dom";

const User = () => {
  return (
    <div className="user-container">
      <div className="user-inner-section">
        <div className="welcome-header">
          <Link to="/">
            <img className="header-logo-image" src={`${process.env.PUBLIC_URL + "logo.png"}`} />
          </Link>
        </div>
        <div className="accout-section">
          <form action="login" method="get" id="login-form">
            <input type="text" name="username" id="id" placeholder="Email or phone number" />
            <input type="password" name="password" id="password" placeholder="Password" />
            <button className="login-button">
              <a href="#" class="login-button-text">
                Login In
              </a>
            </button>
            <a href="#" className="find-information">
              Find Account or Password
            </a>
          </form>
        </div>
      </div>
    </div>
  );
};

export default User;
